                            <p>Hernández Lorenzo, Laura. “Dominick Finello. Ámbitos y espacios pastoriles en obras y autores áureos”, Studia Aurea: revista de literatura española y teoría literaria del Renacimiento y Siglo de Oro, 11, 2017, pp. 625-628.</p>
