import React from 'react'
import github_icon from './assets/github-icon.png'
import researchGate_icon from './assets/researchgate-icon.svg'
import twitter_icon from './assets/twitter-icon.svg'
import academia_icon from './assets/academia-icon.png'

class Footer extends React.Component {
    render() {
        return (
            <div id="footer-container">
                <div>
                    <a href="https://us.academia.edu/LauraHern%C3%A1ndez" target="_blank" rel="noopener noreferrer"><img title="Academia" className="footer-icon" src={academia_icon} alt="academia-icon"></img></a>
                </div>
                <div>
                    <a href="https://www.researchgate.net/institution/Universidad_de_Sevilla" target="_blank" rel="noopener noreferrer"><img title="ResearchGate" className="footer-icon" src={researchGate_icon} alt="researchgate-icon"></img></a>
                </div>
                <div>
                    <a href="https://github.com/lamusadecima" target="_blank" rel="noopener noreferrer"><img title="Github" className="footer-icon" src={github_icon} alt="github-icon"></img></a>
                </div>
                <div>
                    <a href="https://twitter.com/lamusadecima?s=08" target="_blank" rel="noopener noreferrer"><img title="Twitter" className="footer-icon" src={twitter_icon} alt="twitter icon"></img></a>
                </div>
            </div>
        )
    }
}

export default Footer