import React from 'react';
import { sections } from './constants';

class Navbar extends React.Component {

    render() {

        const sectionItems = Object.keys(sections).map(
            (key, index) =>
                <button key={index} onClick={() => this.props.changeSection(sections[key])}>{sections[key]}</button>
        )

        return (
            <nav id="nav-bar">
                {sectionItems}
            </nav>
        )
    }
}

export default Navbar;